* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: #f79f1b;
  font-family: "Roboto Mono", monospace;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.5;
  color: #131313;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  position: relative;
  flex-grow: 1;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.main::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #d9d9d9;
  clip-path: polygon(55% 0, 51% 55%, 48% 50%, 43% 100%, 0 100%, 0 37%, 0 0);
}
@media (max-width: 767px) {
  .main::after {
    clip-path: polygon(55% 0, 26% 58%, 19% 52%, 0 90%, 0 0);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.cases {
  display: flex;
  justify-content: space-between;
  transition: transform 1.5s 0.5s ease-in-out, opacity 1.5s 0.5s ease-in-out;
}
@media (max-width: 1599px) {
  .cases {
    max-width: 1000px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .cases {
    display: block;
    max-width: 400px;
    transition: transform 0.8s 0.5s ease-in-out, opacity 0.8s 0.5s ease-in-out;
  }
}
@media (max-width: 767px) {
  .cases__col:not(:last-child) {
    margin-bottom: 70px;
  }
}

.case {
  display: block;
}
.case img {
  display: block;
  max-width: 100%;
}
.case__head {
  margin-bottom: -85px;
}
@media (max-width: 1599px) {
  .case__head {
    margin-bottom: -55px;
  }
}
@media (max-width: 1023px) {
  .case__head {
    margin-bottom: -10px;
  }
}
.case__box {
  display: inline-block;
  text-align: center;
  color: #fff;
  margin-left: 85px;
}
@media (max-width: 1279px) {
  .case__box {
    margin-left: 40px;
  }
}
@media (max-width: 767px) {
  .case__box {
    display: block;
    margin: 0;
  }
}
.case__title {
  display: inline-block;
  font-size: 20px;
  letter-spacing: 1.68px;
  text-transform: uppercase;
  font-family: "Roboto Mono", monospace;
  padding: 0 25px;
  position: relative;
  margin-bottom: 8px;
}
@media (max-width: 1023px) {
  .case__title {
    padding: 0 16px;
  }
}
.case__title::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #E62346;
  clip-path: polygon(0 50%, 10px 0, calc(100% - 10px) 0, 100% 50%, calc(100% - 10px) 100%, 10px 100%);
  position: absolute;
  left: 0;
  z-index: -1;
}
.case__counter {
  display: block;
  font-size: 40px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  font-family: "Quantico", sans-serif;
  color: #131313;
}
.case__main {
  position: relative;
  z-index: 1;
  cursor: pointer;
  will-change: transform;
  margin-top: 100px;
  transition: transform 0.75s ease-in-out, filter 0.75s ease-in-out;
}
.case__main::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 300px;
  border-radius: 100%;
  background-color: #ffffff;
  transform: translate(-50%, -50%);
  filter: blur(60px);
  z-index: -1;
}
@media (max-width: 1279px) {
  .case__main::after {
    width: 180px;
    height: 180px;
  }
}
@media (max-width: 991px) {
  .case__main::after {
    width: 120px;
    height: 120px;
  }
}
@media (max-width: 767px) {
  .case__main::after {
    width: 180px;
    height: 180px;
  }
}
@media (max-width: 479px) {
  .case__main::after {
    width: 140px;
    height: 140px;
  }
}
@media (max-width: 389px) {
  .case__main::after {
    width: 110px;
    height: 110px;
  }
}
@media (max-width: 1599px) {
  .case__main {
    margin-top: 70px;
  }
}
@media (max-width: 1023px) {
  .case__main {
    margin-top: 30px;
  }
}
.case__hand {
  position: relative;
  transition: transform 0.75s ease-in-out;
}
@media (max-width: 767px) {
  .case__hand {
    left: 40px;
  }
}
@media (max-width: 389px) {
  .case__hand {
    left: 30px;
  }
}
.case__hand img {
  width: 100%;
  object-fit: contain;
}
@media (max-width: 1279px) {
  .case__hand img {
    width: 90%;
  }
}
.case__spiral {
  width: 75%;
  position: absolute;
  left: 35px;
  top: 35%;
  transform: translateY(-25%);
  transition: transform 0.75s ease-in-out;
}
@media (max-width: 767px) {
  .case__spiral {
    width: 90%;
    left: 50%;
    transform: translate(-50%, -30%);
  }
}
.case__spiral img {
  width: 100%;
  object-fit: contain;
}
.case__spiral::before {
  content: "";
  width: 50%;
  height: 50%;
  border-radius: 340px;
  background-color: rgba(255, 255, 255, 0.2);
  filter: blur(42px);
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  transform: translate(-50%, -50%);
}
.case__spiral::after {
  content: "";
  width: 90%;
  height: 50%;
  border-radius: 300px;
  background-color: #fff;
  filter: blur(50px);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.75s ease-in-out;
  opacity: 0;
}
.case__case {
  width: 85%;
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-55%);
  transition: transform 0.75s ease-in-out, filter 0.75s ease-in-out;
}
@media (max-width: 1279px) {
  .case__case {
    left: 5px;
  }
}
@media (max-width: 1023px) {
  .case__case {
    left: 0px;
  }
}
@media (max-width: 767px) {
  .case__case {
    width: 80%;
    top: 40%;
    left: 55%;
    transform: translate(-50%, -45%);
    animation: moving 10s linear 0s infinite normal backwards;
    will-change: transform;
  }
}
@media (max-width: 479px) {
  .case__case {
    width: 70%;
    top: 45%;
    left: 50%;
  }
}
@media (max-width: 389px) {
  .case__case {
    left: 55%;
  }
}
@keyframes moving {
  0% {
    transform: translate(-50%, -45%);
  }
  25% {
    transform: translate(-52%, -43%);
  }
  75% {
    transform: translate(-48%, -47%);
  }
  100% {
    transform: translate(-50%, -45%);
  }
}
.case__case img {
  width: 100%;
  object-fit: contain;
}
.case__skin {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%) scale(0.3);
  opacity: 0;
  transition: transform 0.75s ease-in-out, opacity 0.75s ease-in-out;
}
@media (min-width: 768px) {
  .case:hover .case__main {
    transform: scale(0.8);
  }
  .case:hover .case__hand {
    transform: scale(1.2) translate(23%, 0);
  }
}
@media (min-width: 768px) and (max-width: 767px) {
  .case:hover .case__hand {
    transform: scale(1.1) translate(10%, 0);
  }
}
@media (min-width: 768px) {
  .case:hover .case__spiral {
    transform: translate(30%, -30%);
  }
}
@media (min-width: 768px) and (max-width: 767px) {
  .case:hover .case__spiral {
    transform: translate(-40%, -30%);
  }
}
@media (min-width: 768px) {
  .case:hover .case__spiral::after {
    opacity: 1;
  }
  .case:hover .case__case {
    filter: blur(5px);
    transform: scale(0.6) translate(-50%, -100%);
  }
}
@media (min-width: 768px) and (max-width: 767px) {
  .case:hover .case__case {
    transform: scale(0.75) translate(-120%, -100%);
  }
}
@media (min-width: 768px) {
  .case:hover .case__skin {
    transform: translate(15%, -55%) scale(1.1);
    opacity: 1;
  }
}
@media (min-width: 768px) and (max-width: 767px) {
  .case:hover .case__skin {
    transform: translate(7%, -45%) scale(1);
  }
}

.landing {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 60px 40px;
  width: 100%;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.landing__bg img {
  position: absolute;
  top: -20px;
  left: 0;
  width: 100%;
  height: 110%;
  object-fit: cover;
  z-index: -1;
}
.landing__title {
  max-width: 770px;
  transition: transform 0.8s ease-in-out, opacity 0.8s ease-in-out;
  margin-bottom: 80px;
  font-family: "Quantico", sans-serif;
  color: #131313;
}
