  .landing__title {
    max-width: 670px;
  }
}
@media (max-width: 767px) {
  .landing__title {
    max-width: 600px;
    margin-bottom: 65px;
  }
}
@media (max-width: 620px) {
  .landing__title {
    max-width: 300px;
  }
}
@media (max-width: 479px) {
  .landing__title {
    max-width: 235px;
    word-break: none;
    margin-bottom: 50px;
  }
}
.landing__title i {
  font-family: "Quantico", sans-serif;
}
.landing__title h1,
.landing__title p {
  font-size: 40px;
  line-height: 1.4;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 1279px) {
  .landing__title h1,
  .landing__title p {
    font-size: 36px;
  }
}
@media (max-width: 1023px) {
  .landing__title h1,
  .landing__title p {
    font-size: 32px;
  }
}
@media (max-width: 767px) {
  .landing__title h1,
  .landing__title p {
    font-size: 28px;
  }
}
@media (max-width: 479px) {
  .landing__title h1,
  .landing__title p {
    font-size: 22px;
  }
}
.landing__title p {
  margin: 0;
}
@media (max-width: 767px) {
  .landing__title p {
    margin-bottom: 20px;
