@use 'sass:math';

%svg-common {
	background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-faq {
	@extend %svg-common;
	background-position: 93.15068493150685% 36.95652173913044%;
	background-size: math.div(89, 16)*100% auto;
	width: 16px;

}
@mixin svg-pos-image-faq {
	background-position: 93.15068493150685% 36.95652173913044%;
}
@mixin svg-image-faq-hover {
	.svg-image-faq {
		@include svg-pos-image-faq-hover
	}
}

.svg-image-faq:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(17, 16)*100%;
}

.svg-image-faq-dims {
	width: 16px;
	height: 17px;
}

.svg-image-logo {
	@extend %svg-common;
	background-position: 0 0;
	background-size: math.div(89, 68)*100% auto;
	width: 68px;

}
@mixin svg-pos-image-logo {
	background-position: 0 0;
}
@mixin svg-image-logo-hover {
	.svg-image-logo {
		@include svg-pos-image-logo-hover
	}
}

.svg-image-logo:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(63, 68)*100%;
}

.svg-image-logo-dims {
	width: 68px;
	height: 63px;
}

.svg-image-mail {
	@extend %svg-common;
	background-position: 100% 0;
	background-size: math.div(89, 21)*100% auto;
	width: 21px;

}
@mixin svg-pos-image-mail {
	background-position: 100% 0;
}
@mixin svg-image-mail-hover {
	.svg-image-mail {
		@include svg-pos-image-mail-hover
	}
}

.svg-image-mail:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(17, 21)*100%;
}

.svg-image-mail-dims {
	width: 21px;
	height: 17px;
}


.image-by-height { width:auto; }
.image-by-height:before { content:none; }
.image-by-height > svg { height: inherit; }
.image-by-height > img { height: inherit !important; vertical-align: top !important; margin: 0 !important; padding: 0 !important; max-width: none !important; max-height: none !important; border: 0 !important; opacity: 0 !important; }