@import "./normalize";
@import "./vars";
@import "./mixins";
@import "./fonts";
@import "./sprite_generated";

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"

* {
  box-sizing: border-box // min-height: 0.001vw; // — Fixes Safari bug with viewport units in calc()
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: $bg-color;
  font-family: $font-main;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.5;
  color: $accent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  position: relative;
  flex-grow: 1;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #d9d9d9;
    clip-path: polygon(55% 0, 51% 55%, 48% 50%, 43% 100%, 0 100%, 0 37%, 0 0);
    @include sm-max {
      clip-path: polygon(55% 0, 26% 58%, 19% 52%, 0 90%, 0 0);
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}


.cases {
  display: flex;
  justify-content: space-between;
  transition: transform 1.5s 0.5s ease-in-out, opacity 1.5s 0.5s ease-in-out;

  @include xxl-max {
    max-width: 1000px;
    margin: 0 auto;
  }

  @include sm-max {
    display: block;
    max-width: 400px;
    transition: transform 0.8s 0.5s ease-in-out, opacity 0.8s 0.5s ease-in-out;
  }

  &__col {
    &:not(:last-child) {
      @include sm-max {
        margin-bottom: 70px;
      }
    }
  }
}

.case {
  display: block;

  img {
    display: block;
    max-width: 100%;
  }

  &__head {
    margin-bottom: -85px;

    @include xxl-max {
      margin-bottom: -55px;
    }

    @include lg-max {
      margin-bottom: -10px;
    }
  }

  &__box {
    display: inline-block;
    text-align: center;
    color: #fff;
    margin-left: 85px;

    @include xl-max {
      margin-left: 40px;
    }

    @include sm-max {
      display: block;
      margin: 0;
    }
  }

  &__title {
    display: inline-block;
    font-size: 20px;
    letter-spacing: 1.68px;
    text-transform: uppercase;
    font-family: $font-main;

    padding: 0 25px;
    position: relative;
    margin-bottom: 8px;

    @include lg-max {
      padding: 0 16px;
    }

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background-color: #E62346;
      clip-path: polygon(0 50%, 10px 0, calc(100% - 10px) 0, 100% 50%, calc(100% - 10px) 100%, 10px 100%);
      position: absolute;
      left: 0;
      z-index: -1;
    }
  }

  &__counter {
    display: block;
    font-size: 40px;
    font-weight: 400;
    line-height: 1;
    text-transform: uppercase;
    font-family: $subfont-main;
    color: $accent;
  }

  &__main {
    position: relative;
    z-index: 1;
    cursor: pointer;
    will-change: transform;
    margin-top: 100px;
    transition: transform 0.75s ease-in-out, filter 0.75s ease-in-out;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 300px;
      height: 300px;
      border-radius: 100%;
      background-color: $white;
      transform: translate(-50%, -50%);
      filter: blur(60px);
      z-index: -1;
      @include xl-max {
        width: 180px;
        height: 180px;
      }
      @include md-max {
        width: 120px;
        height: 120px;
      }
      @include sm-max {
        width: 180px;
        height: 180px;
      }
      @include xs-max {
        width: 140px;
        height: 140px;
      }
      @include xxs-max {
        width: 110px;
        height: 110px;
      }
    }
    @include xxl-max {
      margin-top: 70px;
    }
    @include lg-max {
      margin-top: 30px;
    }
  }

  &__hand {
    position: relative;
    transition: transform 0.75s ease-in-out;
    @include sm-max {
      left: 40px;
    }
    @include xxs-max {
      left: 30px;
    }
    img {
      width: 100%;
      object-fit: contain;
      @include xl-max {
        width: 90%;
      }
    }
  }

  &__spiral {
    width: 75%;
    position: absolute;
    left: 35px;
    top: 35%;
    transform: translateY(-25%);
    transition: transform 0.75s ease-in-out;

    @include sm-max {
      width: 90%;
      left: 50%;
      transform: translate(-50%, -30%);
    }

    img {
      width: 100%;
      object-fit: contain;
    }

    &::before {
      content: '';
      width: 50%;
      height: 50%;
      border-radius: 340px;
      background-color: rgba(#fff, 0.2);
      filter: blur(42px);
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      transform: translate(-50%, -50%);
    }

    &::after {
      content: '';
      width: 90%;
      height: 50%;
      border-radius: 300px;
      background-color: #fff;
      filter: blur(50px);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: opacity 0.75s ease-in-out;
      opacity: 0;
    }
  }

  &__case {
    width: 85%;
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-55%);
    transition: transform 0.75s ease-in-out, filter 0.75s ease-in-out;
    @include xl-max {
      left: 5px;
    }
    @include lg-max {
      left: 0px;
    }

    @include sm-max {
      width: 80%;
      top: 40%;
      left: 55%;
      transform: translate(-50%, -45%);
      animation: moving 10s linear 0s infinite normal backwards;
      will-change: transform;
    }
    @include xs-max {
      width: 70%;
      top: 45%;
      left: 50%;
    }
    @include xxs-max {
      left: 55%;
    }

    @keyframes moving {
      0% {
        transform: translate(-50%, -45%);
      }

      25% {
        transform: translate(-52%, -43%);
      }

      75% {
        transform: translate(-48%, -47%);
      }

      100% {
        transform: translate(-50%, -45%);
      }
    }

    img {
      width: 100%;
      object-fit: contain;
    }
  }

  &__skin {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%) scale(0.3);
    opacity: 0;
    transition: transform 0.75s ease-in-out, opacity 0.75s ease-in-out;
  }

  &:hover {
    @include sm-min {
      .case {
        &__main {
          transform: scale(0.8) 
        }
        &__hand {
          transform: scale(1.2) translate(23%, 0);

          @include sm-max {
            transform: scale(1.1) translate(10%, 0);
          }
        }

        &__spiral {
          transform: translate(30%, -30%);

          @include sm-max {
            transform: translate(-40%, -30%);
          }

          &::after {
            opacity: 1;
          }
        }

        &__case {
          filter: blur(5px);
          transform: scale(0.6) translate(-50%, -100%);

          @include sm-max {
            transform: scale(.75) translate(-120%, -100%);
          }
        }

        &__skin {
          transform: translate(15%, -55%) scale(1.1);
          opacity: 1;

          @include sm-max {
            transform: translate(7%, -45%) scale(1);
          }
        }
      }
    }
  }
}

.landing {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 60px 40px;
  width: 100%;
  position: relative;
  z-index: 1;
  overflow: hidden;

  &__bg img{
    position: absolute;
    top: -20px;
    left: 0;
    width: 100%;
    height: 110%;
    object-fit: cover;
    z-index: -1;
  }

  &__title {
    max-width: 770px;
    transition: transform 0.8s ease-in-out, opacity 0.8s ease-in-out;
    margin-bottom: 80px;
    font-family: $subfont-main;
    color: $accent;
    @include lg-max {
      max-width: 670px;
    }
    @include sm-max {
      max-width: 600px;
      margin-bottom: 65px;
    }
    @include ssm-max {
      max-width: 300px;
    }
    @include xs-max {
      max-width: 235px;
      word-break: none;
      margin-bottom: 50px;
    }
    i {
      font-family: $subfont-main;
    }
    h1,
    p {
      font-size: 40px;
      line-height: 1.4;
      font-weight: 700;
      text-align: center;
      text-transform: uppercase;
      @include xl-max {
        font-size: 36px;
      }
      @include lg-max {
        font-size: 32px;
      }
      @include sm-max {
        font-size: 28px;
      }
      @include xs-max {
        font-size: 22px;
      }
    }

    p {
      margin: 0;

      @include sm-max {
        margin-bottom: 20px;
      }
    }

    span {
      display: inline-block;
      padding: 0 18px;
      position: relative;
      font-family: $subfont-main;
      color: #fff;
    }

    a {
      transition: color 0.3s;

      &:hover {
        color: #e62346;
      }
    }
  }

  .cases {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto 70px;

    @include xxl-max {
      max-width: 1000px;
    }

    @include xl-max {
      max-width: 720px;
      margin-bottom: 50px;
    }

    @include lg-max {
      max-width: 550px;
    }

    @include sm-max {
      max-width: 390px;
      margin-bottom: 70px;
    }

    &__col {
      max-width: 47%;

      @include sm-max {
        max-width: none;
      }

      &:first-child {
        .case {
          &__hand {
            transform: scale(-1, 1);
            @include sm-max {
              left: -40px;
            }
            @include xxs-max {
              left: -20px;
            }
          }

          @include sm-min {
            &__head {
              text-align: right;
            }

            &__box {
              margin-left: 0;
              margin-right: 85px;

              @include xl-max {
                margin-right: 40px;
              }
            }

            &__spiral {
              left: initial;
              right: 35px;
            }

            &__case {
              left: initial;
              right: 0px;
              @include xl-max {
                right: -15px;
              }
              @include lg-max {
                right: -10px;
              }
            }
          }

          &:hover {
            @include sm-min {
              .case {
                &__hand {
                  transform: scale(-1.2, 1.2) translate(23%, 0);

                  @include sm-max {
                    transform: scale(-1.1, 1.1) translate(10%, 0);
                  }
                }

                &__spiral {
                  transform: translate(-30%, -30%);

                  @include sm-max {
                    transform: translate(-60%, -30%);
                  }
                }

                &__case {
                  filter: blur(5px);
                  transform: scale(0.6) translate(50%, -100%);

                  @include sm-max {
                    transform: scale(.75) translate(-5%, -100%);
                  }
                }

                &__skin {
                  transform: translate(-25%, -55%) scale(1.1);
                  opacity: 1;

                  @include sm-max {
                    transform: translate(-9%, -45%) scale(1);
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  &__text {
    max-width: 100%;
    font-size: 16px;
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;
    font-family: $font-main;
    color: $accent;
    margin: 0 auto;
    transition: transform 1s 1.25s ease-in-out, opacity 1s 1.25s ease-in-out;

    @include sm-max {
      transition: transform 0.8s 1s ease-in-out, opacity 0.8s 1s ease-in-out;
    }
    @include ssm-max {
      max-width: 280px;
      white-space: normal;
      word-break: keep-all;
    }


    p {
      margin: 0;

      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
    i {
      font-family: $font-main;
      font-style: normal;
      color: #fff;
    }
  }
}

html:not(.is-loaded) {
  .landing__title {
    transform: translateY(100%) scale(0.4);
    opacity: 0;

    @include sm-max {
      transform: translateY(30px);
    }
  }

  .cases {
    transform: scale(0.1);
    opacity: 0;

    @include sm-max {
      transform: translateY(30px);
    }
  }

  .landing__text {
    transform: translateY(100%) scale(0.4);
    opacity: 0;

    @include sm-max {
      transform: translateY(30px);
    }
  }
}

